@import url(https://fonts.googleapis.com/css2?family=Oswald:wght@200;300;400;500;600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100vh;
}

#root {
  height: 100%
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body { font-family: 'Oswald', sans-serif; }

.notification-container {
  position: absolute;
  top: 1vh;
  right: 16vh;
  width: 58.6vh;
  height: auto;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.notification {

  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 27px;
  color: #FFFFFF;

  display: flex;

  padding: 3px 16px;
  margin: 5px;

  transition: opacity 0.4s;

  opacity: 1;

}

.fadeOut {
  opacity: 0;
}

.inform {
  background: radial-gradient(50% 50% at 50% 50%, rgba(43, 57, 98, 0.4) 0%, rgba(36, 39, 63, 0.7) 100%);
  border: 1px solid rgba(0, 0, 0, 0.15);
  box-sizing: border-box;
  box-shadow: 0px 2px 21px rgba(0, 0, 0, 0.47);
  border-radius: 3px;
}

.success {
  background: radial-gradient(50% 50% at 50% 50%, rgba(43, 98, 58, 0.4) 0%, rgba(38, 63, 36, 0.7) 100%);
  border: 1px solid rgba(0, 0, 0, 0.15);
  box-sizing: border-box;
  box-shadow: 0px 2px 21px rgba(0, 0, 0, 0.37);
  border-radius: 3px;
}

.error {
  background: radial-gradient(50% 50% at 50% 50%, rgba(98, 43, 43, 0.4) 0%, rgba(63, 36, 36, 0.7) 100%);
  border: 1px solid rgba(0, 0, 0, 0.15);
  box-sizing: border-box;
  box-shadow: 0px 2px 21px rgba(0, 0, 0, 0.37);
  border-radius: 3px;
}
